.barra{
    display: flex;
    /**flex-direction: column;**/
    justify-content: space-between;
    /*padding-top: 1rem;*/
    @include tablet{
        flex-direction: row;
        justify-content: space-between;
    }
    
}

.navegacion{
    
    @include tablet{
        display: flex;
        align-items: center;
    }

    li{
        color: $azul;
        text-decoration: none;
        font-size: 2.2rem;
        font-weight: $bold;
        display: block;
        text-align: center;
        @include tablet{
            margin-right: 2rem;
            font-size: 1.8rem;
            &:last-of-type{
                margin-right: 0;
            }
        }
        &:hover{
            border-bottom: 3px solid $azul;
        }
    }
}
hr{
    padding-bottom: 0;
}