  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

ul {
  list-style: none; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px; }

/** UI **/
.no-webp .principal {
  background-image: url(../img/manos.jpg); }

.webp .principal {
  background-image: url(../img/manos.webp); }

.header {
  padding: 1rem 0 3rem 0; }

.principal {
  min-height: 40rem;
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center; }
  .principal h1 {
    width: 35rem;
    color: #FFFFFF;
    background-color: rgba(160, 127, 94, 0.5);
    border-radius: 10%;
    padding: 2rem;
    line-height: normal;
    margin-left: 1rem; }
    @media (min-width: 768px) {
      .principal h1 {
        margin-left: 10rem; } }
  .principal button {
    cursor: pointer;
    margin-left: 10rem;
    padding: 1rem 2rem;
    color: #FFFFFF;
    background-color: #e0c009;
    border: none;
    font-weight: 700;
    display: none; }
    @media (min-width: 768px) {
      .principal button {
        display: block; }
        .principal button:hover {
          opacity: 0.7; } }

.footer {
  background-color: #7f00b18f; }
  .footer .social {
    color: #FFFFFF; }
    @media (min-width: 768px) {
      .footer .social {
        display: flex;
        justify-content: left;
        gap: 5rem; } }
    .footer .social li {
      margin: 0 auto; }
    .footer .social h3 {
      text-align: left;
      margin-bottom: 1.5rem; }
    .footer .social a {
      color: #FFFFFF;
      font-size: 2rem; }
  .footer .copyright {
    background-color: #331c9cc5;
    color: #FFFFFF;
    padding: 2.5rem;
    text-align: center; }

.barra {
  display: flex;
  /**flex-direction: column;**/
  justify-content: space-between;
  /*padding-top: 1rem;*/ }
  @media (min-width: 768px) {
    .barra {
      flex-direction: row;
      justify-content: space-between; } }

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center; } }

.navegacion li {
  color: #331c9cc5;
  text-decoration: none;
  font-size: 2.2rem;
  font-weight: 700;
  display: block;
  text-align: center; }
  @media (min-width: 768px) {
    .navegacion li {
      margin-right: 2rem;
      font-size: 1.8rem; }
      .navegacion li:last-of-type {
        margin-right: 0; } }
  .navegacion li:hover {
    border-bottom: 3px solid #331c9cc5; }

hr {
  padding-bottom: 0; }

.intro h3 {
  margin-top: 2rem;
  text-align: left;
  font-weight: 700;
  line-height: 1.2; }

.intro p {
  margin: 0 auto; }

.intro span {
  color: #7f00b18f; }

@media (min-width: 768px) {
  .intro {
    display: grid;
    grid-template-columns: 50% 40%;
    gap: 5rem; } }

.rigth {
  align-self: center; }
  .rigth img {
    display: inline-block;
    border: 1px solid #0000;
    border-radius: 4rem; }

@media (min-width: 768px) {
  .informacion h3 {
    text-align: left;
    margin-left: 3rem; }
  .info {
    display: grid;
    grid-template-columns: 30% 70%;
    column-gap: 2.5rem;
    margin-left: 10rem; } }

.informacion {
  background-color: #7f00b18f;
  max-height: 70rem; }
  .informacion h3 {
    font-weight: 900;
    color: #FFFFFF; }
  .informacion p {
    color: #FFFFFF; }

.info .card {
  background-color: #FFFFFF;
  min-height: 20rem;
  max-width: 20rem;
  margin: 0 auto;
  border-radius: 5%; }
  @media (min-width: 768px) {
    .info .card {
      margin-bottom: 10rem;
      margin-left: 10rem; } }
  .info .card p {
    text-align: center;
    color: #331c9cc5; }
  .info .card h3 {
    color: #331c9cc5; }

.info .card_2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem;
  background-color: #e0c009;
  margin: 1.8rem;
  margin-top: 0; }
  .info .card_2 h3 {
    margin: 0;
    line-height: normal; }

.text p {
  padding-top: 2rem; }

.article {
  min-height: 60rem; }
  @media (min-width: 768px) {
    .article {
      position: relative; } }

.articulo {
  max-width: 120rem;
  margin: 0 auto;
  padding: 5rem;
  border-radius: 1rem;
  text-align: center;
  margin-top: 5rem; }
  @media (min-width: 768px) {
    .articulo {
      position: absolute;
      top: -10rem; } }
  .articulo h3 {
    text-align: center; }
  @media (min-width: 768px) {
    .articulo p {
      padding: 0 7.5rem; } }

.sombra {
  -webkit-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  -moz-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem; }

.oportunity {
  margin-top: 5rem;
  background-color: #7f00b18f;
  margin-bottom: 5rem; }
  .oportunity h2 {
    text-align: left;
    font-weight: 700;
    color: #FFFFFF; }
  .oportunity p {
    margin-bottom: 5rem;
    color: #FFFFFF; }

.servicios h2, .servicios p {
  text-align: center;
  margin: 0 5rem; }

.icono {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 5rem; }
  .icono img {
    max-width: 50%;
    height: fit-content; }
  .icono h3 {
    text-decoration: underline 1px;
    font-size: 1.8rem; }

.iconos-servicios {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr); }
  @media (min-width: 768px) {
    .iconos-servicios {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(4, 1fr); } }
